import React from 'react';
import { withPrefix } from 'gatsby'

import Layout from '../components/Layout';
import Sidebar from '../components/Sidebar';
import config from '../../config';

import camp from '../assets/images/rohingyacamp.jpg';


export default ({data}) => (

	<Layout>
	<Sidebar />
	<div className="container-fluid p-0">
	  <section
	    className="resume-section p-3 p-lg-5 d-flex align-items-center"
	    id="about"
	  >
	    <div className="w-100">
	      <h1 className="mb-0">
	        {config.firstName}
	        <span className="text-primary">{config.lastName}</span>
	      </h1>
	      <div className="subheading mb-5">
	        {config.address} · {config.phone} · {config.email} . {'She/ Her/ Hers'}
	      </div>
	      <div>
	        <table>
	          <td width="71%">
	          <p className="lead mb-5">
	            I am an incoming PhD student in <a href="https://infosci.cornell.edu/" target="_blank" rel="noopener noreferrer">Cornell 
	            Information Science</a> working with <a href="https://www.adityavashistha.com/" target="_blank" 
	            rel="noopener noreferrer">Professor Aditya Vashistha</a>. Before the PhD journey gets into full swing, 
	            I am preparing for my thesis defense at <a href="https://www.buet.ac.bd/web/" target="_blank" 
	            rel="noopener noreferrer">BUET</a>, where I am a second year M.Sc. student in the <a href="https://cse.buet.ac.bd/" 
	            target="_blank" rel="noopener noreferrer">Department of Computer Science and Engineering (CSE)</a>. Additionally, 
	            I am working as a full-time lecturer 
	            of CSE at <a href="https://www.bracu.ac.bd/" target="_blank" rel="noopener noreferrer">Brac University</a>. 
	            <br/><br/>

	            My research focuses on the design, use, and adaptation of existing technologies to meet the needs of resource-stricken communities and 
	            forcibly-displaced populations to uphold their rights to health and digital inclusion. Broadly speaking, my research falls at the intersection of Human-Computer Interaction, Data 
	            Science, and Applied Machine Learning. My goal is not only to solve problems. Rather I aspire to bring solutions that are accessible,
	            adaptable, and acceptable to the communities I am working for.
	            <br/><br/>

	            Now, in between pondering over the research questions, I often go down the rabbit hole of books. My favorite genre
	            is non-fiction. However, fiction always helps mediate between <i>"Is this the real life? Is this just fantasy?"</i>
	          </p>
	          </td>
	          <td width="3%"></td>
	          <td width="26%">
	          <div className="recNews mb-5">
	            Recent News
	          </div>
	          <div className="news">
		          <ul class="fa-ul">
		          <li><span class="fa-li"><i class="fas fa-pen-square"></i></span>Jul 20: Paper on PTSD and free-hand sketches accepted at IMWUT!</li>
		          <li><span class="fa-li"><i class="fas fa-pen-square"></i></span>Apr 20: Paper on dengue epidemic accepted at CSCW!</li>
		          </ul>
	          </div>
	          </td>
	        </table>
	      </div>
	      
	      <div className="social-icons">
	        {config.socialLinks.map(social => {
	          const { icon, url } = social;
	          return (
	            <a key={url} href={url} target="_blank" rel="noopener noreferrer">
	              <i className={icon}></i>
	            </a>
	          );
	        })}
	      </div>
	    </div>
	  </section>

	  <hr className="m-0" />

	  <section
	    className="resume-section p-3 p-lg-5 d-flex justify-content-center"
	    id="publication"
	  >
	    <div className="w-100">
	      <h2 className="mb-5">Publications</h2>

	      <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
	        <div className="resume-content">
	          <h3 className="mb-0">2020</h3>
	          <ul className="papers">
	          <li className="paper"><span className="authorMe">Farhana Shahid</span>, Wasifur Rahman, Mohammad Saifur Rahman, Sharmin Akther Purabi, 
	          Ayesha Seddiqa, Moin Mostakim, Tanjir Rashid Soron, Fahmida Hossain, Nabila Khan, Anika Binte Islam, Nipi Paul, 
	          Ehsan Hoque, and A. B. M. Alim Al Islam. 2020. Leveraging Free-Hand Sketches for Potential Screening of PTSD. To Appear In  
	          <span className="publishedwhere"> Proceedings of the ACM on Interactive Mobile, Wearable and Ubiquitous 
	          Technologies (IMWUT' 20)</span>. Vol. 4, No. 3, Article 94 (September 2020). 
	          [<a href={withPrefix('/files/Sketch_Camera_Ready.pdf')} 
	          target="_blank" rel="noopener noreferrer">PDF</a>] 
	          </li><br/>

	          <li className="paper"><span className="authorMe">Farhana Shahid</span>, Shahinul Hoque Ony, Takrim Rahman Albi, Sriram Chellappan, 
	          Aditya Vashistha, and A. B. M. Alim Al Islam. 2020. <a href="https://dl.acm.org/doi/10.1145/3392875" target="_blank" rel="noopener 
	          noreferrer">Learning from Tweets: Opportunities and Challenges to Inform Policy Making During Dengue Epidemic.</a> In 
	          <span className="publishedwhere"> Proceedings of the ACM on Human-Computer Interaction</span>. Vol. 4, Issue CSCW1, Article 65 (May 2020). 
	          [<a href={withPrefix('/files/cscw_dengue_2020.pdf')}
	          target="_blank" rel="noopener noreferrer">PDF</a>] 
	          </li>
	          </ul>
	        </div>
	      </div>

	      <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
	        <div className="resume-content">
	          <h3 className="mb-0">2019</h3>
	          <ul className="papers">
	          <li className="paper"><span className="authorMe">Farhana Shahid</span>, Wasifur Rahman, Anika Binte Islam, Nipi Paul, Nabila Khan, Mohammad Saifur Rahman, Md Munirul Haque, 
	          and A. B. M. Alim Al Islam. 2019. <a href="https://dl.acm.org/doi/10.1145/3351259" target="_blank" rel="noopener noreferrer">Two 
	          Tell-tale Perspectives of PTSD: Neurobiological Abnormalities and Bayesian Regulatory 
	          Network of the Underlying Disorder in a Refugee Context.</a> In <span className="publishedwhere">Proceedings of the ACM on Interactive Mobile, Wearable and Ubiquitous 
	          Technologies (IMWUT' 19)</span>. Vol. 3, No. 3, Article 101 (September 2019). 
	          [<a href={withPrefix('/files/FSH_IMWUT19.pdf')} 
	          target="_blank" rel="noopener noreferrer">PDF</a>] 
	          [<a href={withPrefix('/files/UbiComp19.pdf')}
	          target="_blank" rel="noopener noreferrer">Talk in UbiComp 2019</a>]</li>
	          </ul>
	        </div>
	      </div>

	    </div>
	  </section>

	  <hr className="m-0" />

	  <section
	    className="resume-section p-3 p-lg-5 d-flex align-items-center"
	    id="research"
	  >
	    <div className="w-100">
	      <h2 className="mb-5">Research</h2>

	      <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
	        <div className="resume-content">
	          <h3 className="mb-0">PTSD within Low-Resource Communities</h3>
	          <table>
	          <tr>
	          <td width="57%">
	          <p className="paper">The critical lack of trained mental health professionals, communication and cultural barriers, 
	          and social stigma around mental health in low-rosurce communities expose the traumatized individuals to greater health risks. 
	          Moreover, the traditional diagnostic tools of PTSD often suffer from various issues associated with human-human interactions 
	          while applying them within resource-stricken populations. In this light, we aim to explore alternative measures to 
	          potentially screen for PTSD using various low-cost, off-the-shelf tools, e.g., portable EEG headsets and free-hand sketching using
	      	  simple pencil and paper. The findings from our field-level work involving refugees and slum-dwellers enable us to identify scopes 
	      	  for improvement in screening the potential cases of PTSD within low-rosurce communities.<br/>
	          Publications: <a href={withPrefix('/files/FSH_IMWUT19.pdf')} target="_blank" rel="noopener noreferrer">IMWUT 2019</a>,
	      		<a href={withPrefix('/files/Sketch_Camera_Ready.pdf')} target="_blank" rel="noopener noreferrer"> IMWUT 2020</a>
	          </p>
	          </td>
	          <td width="3%"></td>
	          <td width="38%">
	          <img className="img-fluid" src={camp} alt="Some female Rohingya refugees are waiting in a room for interview in the camp area"
	          />
	          Kutupalong Rohingya refugee camp, Cox's Bazar, Bangladesh
	          </td>
	          </tr>
	          </table>
	        </div>
	   
	      </div>
	    </div>
	  </section>

	  <hr className="m-0" />

	  <section
	    className="resume-section p-3 p-lg-5 d-flex align-items-center"
	    id="teaching"
	  >
	    <div className="w-100">
	      <h2 className="mb-5">Teaching</h2>

	      <h3 className="mb-5">Appointments</h3>

	      <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h4 className="mb-0">Lecturer</h4>
              <div className="subheading mb-3">Department of Computer Science and Engineering, Brac University</div>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">May 2018 - Present</span>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h4 className="mb-0">Lecturer</h4>
              <div className="subheading mb-3">Department of Computer Science and Engineering, Eastern University</div>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">September 2017 - April 2018</span>
            </div>
          </div>

	      <h3 className="mb-5">Courses Taught</h3>

	      <h4 className="mb-0">Theory</h4>
	      <ul className="fa-ul mb-0">
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Computer Graphics (Summer 2020, Spring 2020, Fall 2019, Summer 2019, Spring 2018)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Pattern Recognition (Summer 2020)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Digital Logic Design (Spring 2020, Summer 2018)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Programming Language I (Fall 2019, Spring 2019, Fall 2018)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Programming Language II (Spring 2019, Fall 2018)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Data Structures (Summer 2019)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Operating Systems (Spring 2018, Fall 2017)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Mathematical Analysis for Computer Science (Fall 2017)
	        </li>
	      </ul>
	      <br/>
	      <h4 className="mb-0">Lab</h4>
	      <ul className="fa-ul mb-0">
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Computer Graphics (Spring 2020, Fall 2019, Fall 2018,  Spring 2019, Summer 2018)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Artificial Intelligence (Spring 2020, Fall 2019)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Object Oriented Programming (Fall 2019, Summer 2019, Spring 2019)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Programming Language I (Summer 2019, Spring 2019, Summer 2018)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Algorithms (Summer 2019, Fall 2019, Summer 2018)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Digital Logic Design (Summer 2019) 
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Data Structures (Summer 2018)
	        </li>
	        <li>
	          <i className="fa-li fa fa-check"></i>
	          Operating Systems (Spring 2018, Fall 2017)
	        </li>
	      </ul>
	    </div>
	  </section>

	  <hr className="m-0" />

	  <section
	    className="resume-section p-3 p-lg-5 d-flex align-items-center"
	    id="contact"
	  >
	    <div className="w-100">
	      <h2 className="mb-5">Contact</h2>
	      <p className="paper">
	      Brac University <br/>
	      UB80815 <br/>
	      46 Mohakhali, C/A <br/>
	      Dhaka - 1212 <br/>
	      Bangladesh <br/>
	      email: <a href={`mailto:${config.email}`}>farhana.shahid@bracu.ac.bd</a><br/>
	      </p>
	    </div>
	  </section>
	</div>
	</Layout>
		
)


